.contacts {

  &__title {
    margin-bottom: 48px;
  }

  &__info {
    margin-bottom: 48px;

    &--last {
      @include on-tablet {
        margin-bottom: 0;
      }
    }

    &-title {
      font-weight: 700;
      font-size: 16px;
      line-height: 27px;
      color: $main-black-color;
      margin-bottom: 16px;
    }

    &-subtitle {
      font-weight: 400;
      font-size: 14px;
      line-height: 27px;
      color: $main-black-color;
      margin-bottom: 4px;
    }

    &-description {
      text-decoration: none;
      font-weight: 700;
      font-size: 16px;
      line-height: 27px;
      color: #3e3e3e;
      margin-bottom: 8px;
      transition: color 0.5s;

      &:hover {
        color: $main-red-color;
      }
    }
  }

  &__field {
    margin-bottom: 16px;
    color: #7E7E83;
    font-family: Cinzel, sans-serif;
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;

    &--last {
      margin-bottom: 32px;
    }
  }

  &__wrap {
    @include on-desktop {
      display: flex;
      justify-content: space-between;
    }
  }
}